import React, { Component } from 'react';
import GoogleMapReact from 'google-map-react';
import axios from "axios";

//const AnyReactComponent = ({ text }) => <div>{text}</div>;

class Map extends Component {
  static defaultProps = {
    center: {
      lat: 59.95,
      lng: 30.33
    },
    zoom: 11
  };

  state = {
    userLocation: { lat: 32, lng: 32 },
    ip: null,
    loading: true
  };

  componentDidMount() {

    axios({
        url: 'https://api.myip.com',
        headers: {
           'Access-Control-Allow-Origin': '*',
           'Access-Control-Allow-Headers': 'Origin, X-Requested-With, Content-Type, Accept',
        }
      })
      .then(response => {
        /*this.setState({
            ip: response.data.ip
        });*/

        axios
      .get(`http://ip-api.com/json/${response.data.ip}`)
      .then(response => {

        this.setState({
            userLocation: { lat: response.data.lat, lng: response.data.lon },
            loading: false
          });

        this.props.center.lat = response.data.lat;
        this.props.center.lng = response.data.lon;

        //console.log(this.props.center);
      })
      .catch(error => console.log(error));



      })
      .catch(error => console.log(error));

    
    
  }

  render() {
    const { loading } = this.state;
    if (loading) {
        return null;
    }
    return (
      // Important! Always set the container height explicitly
      <div style={{ height: '90vh', width: '100%' }} >
        <GoogleMapReact
          //bootstrapURLKeys={{ key: 'AIzaSyB3Xih2gWO1QOlsf923jOojGgsTEL3dRLU' }}
          defaultCenter={this.props.center}
          defaultZoom={this.props.zoom}
        
        />
      </div>
    );
  }
}

export default Map;